import AdopsjonStartdatoValg from 'app/steps/uttaksplan-info/components/scenarios/mor-far-adopsjon/adopsjonStartdatoValg';

/* eslint-disable @typescript-eslint/no-empty-interface */
interface UttaksplanInfo {}

export interface MorFdselUttaksplanInfo extends UttaksplanInfo {
    permisjonStartdato: string;
    skalIkkeHaUttakFrTermin: boolean;
    fellesperiodeukerMor: number | undefined;
}

export interface MorFarAdopsjonUttaksplanInfo extends UttaksplanInfo {
    harAnnenForelderSktFP: boolean;
    startdatoAdopsjonValg: AdopsjonStartdatoValg;
    annenStartdatoAdopsjon: string;
    annenForeldersSisteDag: string;
    skersFrsteDag: string;
    antallUkerFellesperiode: string;
    antallDagerFellesperiode: string;
    fellesperiodeukerMor: number | undefined;
}

export interface FarMedmorAleneomsorgFdselUttaksplanInfo extends UttaksplanInfo {
    startdatoUttak: string;
    fellesperiodeukerMor: number | undefined;
}

export interface FarMedmorFdselOgMorHarIkkeRettUttaksplanInfo extends UttaksplanInfo {
    permisjonStartdato: string;
}

export interface FarMedmorFrstegangssknadMedAnnenPartUttaksplanInfo extends UttaksplanInfo {
    permisjonStartdato: string;
}

export interface FarMedmorFdselBeggeHarRettUttaksplanInfo extends UttaksplanInfo {
    morsSisteDag: string;
    farMedmorsFrsteDag: string;
    antallUkerFellesperiode: string;
    antallDagerFellesperiode: string;
}

export interface MorFarAdopsjonAnnenForelderHarRettIESUttaksplanInfo extends UttaksplanInfo {
    startdatoAdopsjonValg: AdopsjonStartdatoValg;
    dekningsgrad: string;
    annenStartdatoAdopsjon: string | undefined;
}

export interface MorFarFdselAnnenForelderHarRettIESUttaksplanInfo extends UttaksplanInfo {
    permisjonStartdato: string;
    skalIkkeHaUttakFrTermin: boolean | undefined;
    dekningsgrad: string;
}

export const isFarMedmorFdselBeggeHarRettUttaksplanInfo = (
    uttaksplanInfo: UttaksplanInfo
): uttaksplanInfo is FarMedmorFdselBeggeHarRettUttaksplanInfo => {
    return 'morsSisteDag' in uttaksplanInfo;
};

export default UttaksplanInfo;
