import { Attachment } from 'app/types/Attachment';
import { AttachmentType } from 'app/types/AttachmentType';
import { ForeldrepengesknadContextAction, ForeldrepengesknadContextActionKeys } from '../action/actionCreator';
import { ForeldrepengesknadContextState, foreldrepengesknadInitialState } from '../ForeldrepengesknadContextConfig';

const finnVedleggAvType = (type: AttachmentType, alleVedlegg: Attachment[]): Attachment[] | undefined => {
    const vedleggAvType = alleVedlegg.filter((v) => v.type === type);
    return vedleggAvType.length > 0 ? vedleggAvType : undefined;
};

const foreldrepengesknadReducer = (
    state: ForeldrepengesknadContextState,
    action: ForeldrepengesknadContextAction
): ForeldrepengesknadContextState => {
    switch (action.type) {
        case ForeldrepengesknadContextActionKeys.SET_HARGODKJENTVILKR:
            return {
                ...state,
                sknad: {
                    ...state.sknad,
                    harGodkjentVilkr: action.payload,
                },
            };
        case ForeldrepengesknadContextActionKeys.SET_ER_ENDRINGSSKNAD:
            return {
                ...state,
                sknad: {
                    ...state.sknad,
                    erEndringssknad: action.payload,
                },
            };
        case ForeldrepengesknadContextActionKeys.SET_SKERSITUASJON:
            return {
                ...state,
                sknad: {
                    ...state.sknad,
                    skersituasjon: action.payload,
                },
            };
        case ForeldrepengesknadContextActionKeys.SET_OMBARNET:
            return {
                ...state,
                sknad: {
                    ...state.sknad,
                    barn: {
                        ...action.payload,
                    },
                },
            };
        case ForeldrepengesknadContextActionKeys.SET_ANNENFORELDER:
            return {
                ...state,
                sknad: {
                    ...state.sknad,
                    annenForelder: action.payload,
                },
            };
        case ForeldrepengesknadContextActionKeys.SET_INFORMASJON_OM_UTENLANDSOPPHOLD:
            return {
                ...state,
                sknad: {
                    ...state.sknad,
                    informasjonOmUtenlandsopphold: {
                        ...state.sknad.informasjonOmUtenlandsopphold,
                        ...action.payload,
                    },
                },
            };
        case ForeldrepengesknadContextActionKeys.AVBRYT_SKNAD:
            return {
                ...foreldrepengesknadInitialState,
                skerinfo: state.skerinfo,
                saker: state.saker,
            };
        case ForeldrepengesknadContextActionKeys.UPDATE_CURRENT_ROUTE:
            return {
                ...state,
                currentRoute: action.payload,
            };
        case ForeldrepengesknadContextActionKeys.APPLY_STORED_STATE:
            return {
                ...state,
                ...action.payload,
            };

        case ForeldrepengesknadContextActionKeys.SET_SAKER:
            return {
                ...state,
                saker: [...action.payload],
            };
        case ForeldrepengesknadContextActionKeys.SET_SKERINFO:
            return {
                ...state,
                skerinfo: { ...action.payload },
            };
        case ForeldrepengesknadContextActionKeys.SET_SKER:
            return {
                ...state,
                sknad: {
                    ...state.sknad,
                    sker: {
                        ...action.payload,
                    },
                },
            };
        case ForeldrepengesknadContextActionKeys.SET_UTTAKSPLAN_INFO:
            return {
                ...state,
                uttaksplanInfo: { ...action.payload },
            };
        case ForeldrepengesknadContextActionKeys.SET_DEKNINGSGRAD:
            return {
                ...state,
                sknad: {
                    ...state.sknad,
                    dekningsgrad: action.dekningsgrad,
                },
            };
        case ForeldrepengesknadContextActionKeys.SET_EKSISTERENDE_SAK:
            return {
                ...state,
                eksisterendeSak: action.payload,
            };
        case ForeldrepengesknadContextActionKeys.SET_BARN_FRA_NESTE_SAK:
            return {
                ...state,
                barnFraNesteSak: action.payload,
            };
        case ForeldrepengesknadContextActionKeys.SET_VEDLEGG:
            //TODO Legg til hndtering av fleire vedlegg
            return {
                ...state,
                sknad: {
                    ...state.sknad,
                    barn: {
                        ...state.sknad.barn,
                        dokumentasjonAvAleneomsorg:
                            finnVedleggAvType(AttachmentType.ALENEOMSORG, action.vedlegg) ||
                            state.sknad.barn.dokumentasjonAvAleneomsorg,
                    },
                },
            };
        case ForeldrepengesknadContextActionKeys.LAG_UTTAKSPLANFORSLAG:
        case ForeldrepengesknadContextActionKeys.SLETT_UTTAKSPLAN:
            return {
                ...state,
                sknad: {
                    ...state.sknad,
                    uttaksplan: action.payload,
                },
            };
        case ForeldrepengesknadContextActionKeys.SET_UTTAKSPLAN:
            return {
                ...state,
                sknad: {
                    ...state.sknad,
                    uttaksplan: action.payload,
                },
            };
        case ForeldrepengesknadContextActionKeys.SET_PERIODER_SOM_SKAL_SENDES_INN:
            return {
                ...state,
                perioderSomSkalSendesInn: action.payload,
            };
        case ForeldrepengesknadContextActionKeys.SET_SKNAD:
            return {
                ...state,
                sknad: {
                    ...state.sknad,
                    ...action.payload,
                },
            };
        case ForeldrepengesknadContextActionKeys.SET_SKNAD_GJELDER_ET_NYTT_BARN:
            return {
                ...state,
                sknadGjelderEtNyttBarn: action.payload,
            };
        case ForeldrepengesknadContextActionKeys.SET_GODKJENT_OPPSUMMERING:
            return {
                ...state,
                sknad: {
                    ...state.sknad,
                    harGodkjentOppsummering: action.payload,
                },
            };

        case ForeldrepengesknadContextActionKeys.SET_TILLEGGSOPPLYSNINGER:
            return {
                ...state,
                sknad: {
                    ...state.sknad,
                    tilleggsopplysninger: action.payload,
                },
            };
        case ForeldrepengesknadContextActionKeys.SET_KVITTERING:
            return {
                ...state,
                kvittering: action.payload,
            };
        case ForeldrepengesknadContextActionKeys.SET_ANTALL_UKER_I_UTTAKSPLAN:
            return {
                ...state,
                antallUkerIUttaksplan: action.payload,
            };
        case ForeldrepengesknadContextActionKeys.SET_ENDRINGSTIDSPUNKT:
            return {
                ...state,
                endringstidspunkt: action.payload,
            };
        case ForeldrepengesknadContextActionKeys.SET_SPRKKODE:
            return {
                ...state,
                sknad: {
                    ...state.sknad,
                    sker: {
                        ...state.sknad.sker,
                        sprkkode: action.payload,
                    },
                },
            };
        case ForeldrepengesknadContextActionKeys.SET_UTTAKSPLAN_SLETTET:
            return {
                ...state,
                harUttaksplanBlittSlettet: action.uttaksplanHarBlittSlettet,
            };
        case ForeldrepengesknadContextActionKeys.SET_NSKER_JUSTERT_UTTAK_VED_FDSEL:
            return {
                ...state,
                sknad: {
                    ...state.sknad,
                    nskerJustertUttakVedFdsel: action.payload,
                },
            };
        case ForeldrepengesknadContextActionKeys.SET_BRUKER_SVARTE_JA_P_AUTO_JUSTERING:
            return {
                ...state,
                brukerSvarteJaPAutoJustering: action.payload,
            };
        case ForeldrepengesknadContextActionKeys.SET_ANNEN_PARTS_UTTAK_ER_LAGT_TIL_I_PLAN:
            return {
                ...state,
                annenPartsUttakErLagtTilIPlan: action.payload,
            };
        default:
            return state;
    }
};

export default foreldrepengesknadReducer;
