export const mapTidsperiodeStringToTidsperiode = (t: Partial<Tidsperiode>): Partial<TidsperiodeDate> => {
    return {
        fom: ISOStringToDate(t.fom),
        tom: ISOStringToDate(t.tom),
    };
};

export const mapTidsperiodeToTidsperiodeString = (t: Partial<TidsperiodeDate>): Partial<Tidsperiode> => {
    return {
        fom: dateToISOString(t.fom),
        tom: dateToISOString(t.tom),
    };
};

export const stillingsprosentIsMoreThan0 = (stillingsprosent: string): boolean => {
    const pst = getFloatFromString(stillingsprosent);
    if (pst) {
        return pst > 0;
    }
    return false;
};

export const samtidigUttakProsentIsMax100 = (samtidigUttakProsent: string): boolean => {
    const pst = getFloatFromString(samtidigUttakProsent);

    if (pst) {
        return pst <= 100;
    }

    return false;
};

export const stillingsprosentIsLessThan100 = (stillingsprosent: string): boolean => {
    const pst = getFloatFromString(stillingsprosent);
    if (pst) {
        return pst < 100;
    }
    return false;
};

const isValidStillingsprosent = (pst: string | undefined): boolean =>
    pst !== undefined && isNaN(parseFloat(pst)) === false;

const prettifyProsent = (pst: string | undefined): number | undefined => {
    if (pst === undefined) {
        return undefined;
    }

    const nbr = parseFloat(pst);
    if (isNaN(nbr)) {
        return undefined;
    }
    if (Math.round(nbr) === nbr) {
        return Math.round(nbr);
    }
    return nbr;
};

export const getUttaksprosentFromStillingsprosent = (
    stillingsPst: number | undefined,
    samtidigUttakPst: number | undefined
): number | undefined => {
    if (samtidigUttakPst) {
        return samtidigUttakPst;
    }
    if (stillingsPst) {
        let prosent = (100 - stillingsPst) * 100;
        prosent = Math.round(prosent) / 100;

        return prosent;
    }
    return undefined;
};

export const getOppholdskontoNavn = (
    intl: IntlShape,
    rsak: OppholdrsakType,
    foreldernavn: string,
    erMor: boolean
) => {
    const navn = capitalizeFirstLetter(foreldernavn);
    return erMor
        ? intlUtils(intl, `uttaksplan.oppholdsrsaktype.foreldernavn.far.${rsak}`, { foreldernavn: navn })
        : intlUtils(intl, `uttaksplan.oppholdsrsaktype.foreldernavn.mor.${rsak}`, { foreldernavn: navn });
};

export const getStnadskontoFromOppholdsrsak = (rsak: OppholdrsakType): StnadskontoType => {
    if (rsak === OppholdrsakType.UttakFedrekvoteAnnenForelder) {
        return StnadskontoType.Fedrekvote;
    }

    if (rsak === OppholdrsakType.UttakMdrekvoteAnnenForelder) {
        return StnadskontoType.Mdrekvote;
    }

    if (rsak === OppholdrsakType.UttakFellesperiodeAnnenForelder) {
        return StnadskontoType.Fellesperiode;
    }

    if (rsak === OppholdrsakType.UttakForeldrepengerAnnenForelder) {
        return StnadskontoType.Foreldrepenger;
    }

    return StnadskontoType.ForeldrepengerFrFdsel;
};

export const getOppholdsrsakFromStnadskonto = (konto: StnadskontoType): OppholdrsakType | undefined => {
    switch (konto) {
        case StnadskontoType.Fedrekvote:
            return OppholdrsakType.UttakFedrekvoteAnnenForelder;
        case StnadskontoType.Mdrekvote:
            return OppholdrsakType.UttakMdrekvoteAnnenForelder;
        case StnadskontoType.Fellesperiode:
            return OppholdrsakType.UttakFellesperiodeAnnenForelder;
        default:
            return undefined;
    }
};

export const getForelderFromPeriode = (periode: Periode): Forelder | undefined => {
    if (!isHull(periode) && !isPeriodeUtenUttak(periode)) {
        return periode.forelder;
    }

    return undefined;
};

export const getForelderNavn = (forelder: Forelder, navnPForeldre: NavnPForeldre): string => {
    if (navnPForeldre.farMedmor) {
        return forelder === Forelder.mor ? navnPForeldre.mor : navnPForeldre.farMedmor;
    }
    return forelder === Forelder.mor ? navnPForeldre.mor : forelder;
};

export const getPeriodeTittel = (
    intl: IntlShape,
    periode: Periode,
    navnPForeldre: NavnPForeldre,
    familiehendelsesdato: Date,
    termindato: Date | undefined,
    situasjon: Situasjon,
    erFarEllerMedmor?: boolean,
    erAleneOmOmsorg?: boolean
): string => {
    switch (periode.type) {
        case Periodetype.Uttak: {
            const tittelMedNavn = getStnadskontoNavn(
                intl,
                periode.konto,
                navnPForeldre,
                erFarEllerMedmor,
                erAleneOmOmsorg
            );
            const tittel = appendPeriodeNavnHvisUttakRundtFdselFarMedmor(
                intl,
                tittelMedNavn,
                periode,
                situasjon,
                familiehendelsesdato,
                termindato
            );
            if (
                (periode.gradert && isValidStillingsprosent(periode.stillingsprosent)) ||
                (periode.nskerSamtidigUttak && isValidStillingsprosent(periode.samtidigUttakProsent))
            ) {
                return `${tittel} ${intlUtils(intl, 'gradering.prosent', {
                    stillingsprosent: getUttaksprosentFromStillingsprosent(
                        prettifyProsent(periode.stillingsprosent),
                        periode.samtidigUttakProsent ? prettifyProsent(periode.samtidigUttakProsent) : undefined
                    ),
                })}`;
            }

            return tittel;
        }
        case Periodetype.PeriodeUtenUttak:
            return intlUtils(intl, 'uttaksplan.periodetype.periodeUtenUttak.tittel');
        case Periodetype.Overfring:
            return getStnadskontoNavn(intl, periode.konto, navnPForeldre);
        case Periodetype.Utsettelse:
            if (periode.rsak) {
                return intlUtils(intl, 'uttaksplan.periodeliste.utsettelsesrsak', {
                    rsak: intlUtils(intl, `uttaksplan.utsettelsesrsak.${periode.rsak}`),
                });
            }
            return intlUtils(intl, 'uttaksplan.periodeliste.utsettelsesrsak.ukjent');
        case Periodetype.Opphold:
            return getOppholdskontoNavn(
                intl,
                periode.rsak,
                getForelderNavn(periode.forelder, navnPForeldre),
                periode.forelder === 'mor'
            );
        case Periodetype.Hull:
            return intlUtils(intl, 'uttaksplan.periodetype.hull.tittel');
        case Periodetype.Info:
            switch (periode.infotype) {
                case PeriodeInfoType.uttakAnnenPart:
                    return getUttakAnnenPartStnadskontoNavn(
                        intl,
                        getStnadskontoFromOppholdsrsak(periode.rsak),
                        periode.forelder,
                        navnPForeldre,
                        periode.samtidigUttakProsent
                    );
                case PeriodeInfoType.utsettelseAnnenPart:
                    return intlUtils(intl, `uttaksplan.periodetype.info.utsettelse.${periode.rsak}`, {
                        navn: getForelderNavn(periode.forelder, navnPForeldre),
                    });
                default:
                    return intlUtils(intl, `uttaksplan.periodetype.info.${periode.infotype}`);
            }
    }
};

export const erSentGradertUttak = (periode: Periode) =>
    periode.type === Periodetype.Uttak &&
    !isDateTodayOrInTheFuture(dateToISOString(periode.tidsperiode.fom)) &&
    periode.gradert;

export const erPeriodeInnvilget = (periode: Periode, eksisterendeSak?: EksisterendeSak): boolean => {
    if (eksisterendeSak === undefined) {
        return false;
    }
    const saksperiode = getSaksperiode(periode, eksisterendeSak);
    return saksperiode ? saksperiode.resultat.innvilget : false;
};

const getSaksperiode = (periode: Periode, ekisterendeSak: EksisterendeSak) => {
    return ekisterendeSak.saksperioder.find((saksperiode) =>
        erTidsperioderLike(convertTidsperiodeToTidsperiodeDate(saksperiode.periode), periode.tidsperiode)
    );
};

export const getPeriodeForelderNavn = (periode: Periode, navnPForeldre: NavnPForeldre): string => {
    if (
        periode.type === Periodetype.Utsettelse ||
        periode.type === Periodetype.Uttak ||
        periode.type === Periodetype.Overfring ||
        periode.type === Periodetype.Opphold ||
        periode.type === Periodetype.Info
    ) {
        return getForelderNavn(periode.forelder, navnPForeldre);
    }
    return 'Ingen forelder registrert';
};

export const getSamtidigUttakEllerGraderingsProsent = (periode: UttakAnnenPartInfoPeriode): number | undefined => {
    const periodeErGradert = periode.stillingsprosent !== undefined;
    const periodeErSamtidigUttak = periode.samtidigUttakProsent !== undefined;

    if (periodeErSamtidigUttak) {
        return (100 - getFloatFromString(periode.samtidigUttakProsent)!) / 100;
    }

    if (periodeErGradert) {
        return getFloatFromString(periode.stillingsprosent)! / 100;
    }

    return undefined;
};

export const justerAndrePartsUttakAvFellesperiodeOmMulig = (
    perioder: Periode[],
    uttakFellesperiode: StnadskontoUttak | undefined
): Periode[] => {
    if (uttakFellesperiode === undefined || uttakFellesperiode.dager >= 0 || perioder.length === 0) {
        return perioder;
    }

    const dagerGjenstendeFellesperiode = uttakFellesperiode.dager;

    const sisteFellesperiodeAnnenPart = [...perioder]
        .reverse()
        .find((p) => isAnnenPartInfoPeriode(p) && p.rsak === OppholdrsakType.UttakFellesperiodeAnnenForelder);

    if (sisteFellesperiodeAnnenPart !== undefined && isAnnenPartInfoPeriode(sisteFellesperiodeAnnenPart)) {
        const dagerMedFellesperiodeISistePeriode = Perioden(sisteFellesperiodeAnnenPart).getAntallUttaksdager();
        const justeringsProsent = getSamtidigUttakEllerGraderingsProsent(sisteFellesperiodeAnnenPart) || 1;
        const diff = dagerGjenstendeFellesperiode / justeringsProsent + dagerMedFellesperiodeISistePeriode;
        const indexSistePeriode = perioder.findIndex((p) => p.id === sisteFellesperiodeAnnenPart.id);

        if (dagerGjenstendeFellesperiode < 0 && diff > 0) {
            perioder[indexSistePeriode] = {
                ...sisteFellesperiodeAnnenPart,
                tidsperiode: Tidsperioden(sisteFellesperiodeAnnenPart.tidsperiode).setUttaksdager(diff),
            };
            return perioder;
        }

        if (dagerGjenstendeFellesperiode < 0 && diff === 0) {
            return perioder.splice(indexSistePeriode, 1);
        }
    }

    return perioder;
};

export const getSlettPeriodeTekst = (periodetype: Periodetype): string => {
    switch (periodetype) {
        case Periodetype.Uttak:
            return 'uttaksplan.slettPeriode.uttak';
        case Periodetype.Overfring:
            return 'uttaksplan.slettPeriode.overfring';
        case Periodetype.Opphold:
            return 'uttaksplan.slettPeriode.opphold';
        case Periodetype.Utsettelse:
            return 'uttaksplan.slettPeriode.utsettelse';
        default:
            return '';
    }
};

const erPeriodeFomEllerEtterDato = (periode: Periode, dato: Date): boolean => {
    return (
        periode.tidsperiode.fom !== undefined &&
        periode.tidsperiode.tom !== undefined &&
        dayjs(periode.tidsperiode.fom).isSameOrAfter(dato, 'day') &&
        dayjs(periode.tidsperiode.tom).isSameOrAfter(dato, 'day')
    );
};

export const erPeriodeFrDato = (periode: Periode, dato: Date) => {
    return erPeriodeFomEllerEtterDato(periode, dato) === false;
};

export const erGradering = (periode: Periode) => periode.type === Periodetype.Uttak && periode.gradert === true;

export const erUttakEllerOppholdMerEnnTreMnederSiden = (periode: Periode) =>
    (periode.type === Periodetype.Uttak || periode.type === Periodetype.Opphold) &&
    dayjs(periode.tidsperiode.fom).isBefore(dayjs().startOf('day').subtract(3, 'months'), 'day');

export const erUtsettelsePgaSykdom = (periode: Utsettelsesperiode) =>
    periode.rsak === UtsettelsersakType.Sykdom ||
    periode.rsak === UtsettelsersakType.InstitusjonSker ||
    periode.rsak === UtsettelsersakType.InstitusjonBarnet;

export const erUttakGrunnetSykdom = (periode: Periode) => {
    if (
        isOverfringsperiode(periode) &&
        (periode.rsak === OverfringrsakType.institusjonsoppholdAnnenForelder ||
            periode.rsak === OverfringrsakType.sykdomAnnenForelder)
    ) {
        return true;
    }

    if (isUttaksperiode(periode)) {
        if (
            periode.erMorForSyk === true ||
            periode.morsAktivitetIPerioden === MorsAktivitet.TrengerHjelp ||
            periode.morsAktivitetIPerioden === MorsAktivitet.Innlagt
        ) {
            return true;
        }
    }

    if (
        isUttakAvFellesperiode(periode) &&
        (periode.morsAktivitetIPerioden === MorsAktivitet.Innlagt ||
            periode.morsAktivitetIPerioden === MorsAktivitet.TrengerHjelp)
    ) {
        return true;
    }

    return false;
};

export const erUttakTilbakeITid = (periode: Periode) =>
    isUttaksperiode(periode) && !isDateInTheFuture(dateToISOString(periode.tidsperiode.fom));

export const erUtsettelseTilbakeITid = (periode: Periode) =>
    periode.type === Periodetype.Utsettelse && !isDateInTheFuture(dateToISOString(periode.tidsperiode.fom));

export const erUtsettelseGrunnetPgaArbeid = (periode: Utsettelsesperiode) =>
    periode.rsak === UtsettelsersakType.Arbeid;

export const erUtsettelse = (periode: Periode) => periode.type === Periodetype.Utsettelse;

const erUtsettelsePgaFerieEllerArbeid = (periode: Periode) =>
    periode.type === Periodetype.Utsettelse &&
    (periode.rsak === UtsettelsersakType.Ferie || periode.rsak === UtsettelsersakType.Arbeid);

export const erSenUtsettelsePgaFerieEllerArbeid = (periode: Periode) =>
    erUtsettelseTilbakeITid(periode) && erUtsettelsePgaFerieEllerArbeid(periode);

export const errsakSykdomEllerInstitusjonsopphold = (rsak: UtsettelsersakType | OverfringrsakType) =>
    rsak === UtsettelsersakType.Sykdom ||
    rsak === UtsettelsersakType.InstitusjonBarnet ||
    rsak === UtsettelsersakType.InstitusjonSker ||
    rsak === OverfringrsakType.institusjonsoppholdAnnenForelder ||
    rsak === OverfringrsakType.sykdomAnnenForelder;

export const finnesPeriodeIOpprinneligPlan = (periode: Periode, opprinneligPlan: Periode[]): boolean => {
    return opprinneligPlan.some((op) => Perioden(periode).erLik(op, true, true));
};

export const getAnnenForelderSamtidigUttakPeriode = (periode: Periode, perioder: Periode[]): Periode | undefined => {
    if (isUttaksperiode(periode)) {
        const samtidigUttak = perioder
            .filter((p) => isUttakAnnenPart(p))
            .find(
                (p) =>
                    isUttakAnnenPart(p) &&
                    dayjs(periode.tidsperiode.fom).isSame(p.tidsperiode.fom) &&
                    p.nskerSamtidigUttak === true &&
                    p.id !== periode.id
            );

        return samtidigUttak !== undefined ? samtidigUttak : undefined;
    }

    return undefined;
};
