import dayjs from 'dayjs';
import validator from '@navikt/fnrvalidator';

type FdselsnummerValidationResult = false | 'fnr' | 'dnr' | 'hnr';
export const isFdselsnummerFormatValid = (fnr: string): FdselsnummerValidationResult => {
    const result = validator.idnr(fnr);

    if (result.status !== 'valid') {
        return false;
    }

    return result.type;
};

export const isSixteenOrOlder = (fnr: string, isFdselsnummerValid: FdselsnummerValidationResult): boolean => {
    const dato =
        isFdselsnummerValid === 'dnr' ? `${Number(fnr.substr(0, 1)) - 4}${fnr.substr(1, 1)}` : fnr.substr(0, 2);
    const mnd = fnr.substr(2, 2);
    const r = fnr.substr(4, 2);

    let fdselsdato = dayjs(`${dato}-${mnd}-${r}`, 'DD-MM-YY');

    if (fdselsdato.get('year') > dayjs().get('year')) {
        fdselsdato = fdselsdato.subtract(100, 'year');
    }

    if (!fdselsdato.isValid()) {
        return false;
    }

    return fdselsdato.isBefore(dayjs().subtract(16, 'year'), 'day');
};
