import { intlUtils } from '@navikt/fp-common';
import { Forelder } from 'app/types/Forelder';
import { NavnPForeldre } from 'app/types/NavnPForeldre';
import { TilgjengeligStnadskonto } from 'app/types/TilgjengeligStnadskonto';
import { capitalizeFirstLetter } from 'app/utils/stringUtils';
import { IntlShape } from 'react-intl';
import { StnadskontoType } from 'uttaksplan/types/StnadskontoType';
import { getForelderNavn } from './periodeUtils';

export const getVelgbareStnadskontotyper = (stnadskontoTyper: TilgjengeligStnadskonto[]): StnadskontoType[] =>
    stnadskontoTyper
        .filter(
            (kontoType) =>
                kontoType.konto === StnadskontoType.Fellesperiode ||
                kontoType.konto === StnadskontoType.Fedrekvote ||
                kontoType.konto === StnadskontoType.Mdrekvote ||
                kontoType.konto === StnadskontoType.Foreldrepenger ||
                kontoType.konto === StnadskontoType.AktivitetsfriKvote
        )
        .map((kontoType) => kontoType.konto);

export const getStnadskontoNavn = (
    intl: IntlShape,
    konto: StnadskontoType,
    navnPForeldre: NavnPForeldre,
    erFarEllerMedmor?: boolean,
    erAleneOmOmsorg?: boolean
) => {
    let navn;

    switch (konto) {
        case StnadskontoType.Mdrekvote:
            navn = navnPForeldre.mor;
            break;
        case StnadskontoType.Fedrekvote:
            navn = navnPForeldre.farMedmor;
            break;
        default:
            navn = undefined;
    }

    if (navn) {
        return intl.formatMessage(
            { id: 'uttaksplan.stnadskontotype.foreldernavn.kvote' },
            { navn: getNavnGenitivEierform(capitalizeFirstLetter(navn), intl.locale) }
        );
    }

    if (erFarEllerMedmor === true && erAleneOmOmsorg === false) {
        if (konto === StnadskontoType.AktivitetsfriKvote) {
            return intl.formatMessage({ id: 'uttaksplan.stnadskontotype.AKTIVITETSFRI_KVOTE_BFHR' });
        }
        if (konto === StnadskontoType.Foreldrepenger) {
            return intl.formatMessage({ id: 'uttaksplan.stnadskontotype.AKTIVITETSKRAV_KVOTE_BFHR' });
        }
    }
    return intl.formatMessage({ id: `uttaksplan.stnadskontotype.${konto}` });
};

export const getUttakAnnenPartStnadskontoNavn = (
    intl: IntlShape,
    konto: StnadskontoType,
    periodeForelder: Forelder,
    navnPForeldre: NavnPForeldre,
    samtidigUttakProsent: string | undefined,
    erFarEllerMedmor?: boolean,
    erAleneOmOmsorg?: boolean
) => {
    const forelderNavn = getForelderNavn(periodeForelder, navnPForeldre);
    if (samtidigUttakProsent !== undefined) {
        const navn = getNavnGenitivEierform(forelderNavn, intl.locale);
        const intlTekst =
            konto === StnadskontoType.Fellesperiode
                ? 'uttaksplan.periodeAnnenPart.tittel.gradertEllerSamtidigUttakFellesperiode'
                : 'uttaksplan.periodeAnnenPart.tittel.gradertEllerSamtidigUttak';
        return intlUtils(intl, intlTekst, {
            navn: capitalizeFirstLetter(navn),
            prosent: samtidigUttakProsent,
        });
    }
    return getStnadskontoNavn(intl, konto, navnPForeldre, erFarEllerMedmor, erAleneOmOmsorg);
};

const navnSlutterPSLyd = (navn: string): boolean => {
    const sisteBokstav = navn.charAt(navn.length - 1).toLowerCase();
    return sisteBokstav === 's' || sisteBokstav === 'x' || sisteBokstav === 'z';
};

export const getNavnGenitivEierform = (navn: string, locale: string): string => {
    if (locale !== 'nb') {
        return navn;
    }
    if (navnSlutterPSLyd(navn)) {
        return `${navn}'`;
    }
    return `${navn}s`;
};
