import { isValidTidsperiode } from 'app/steps/uttaksplan-info/utils/Tidsperioden';
import { Uttaksdagen } from 'app/steps/uttaksplan-info/utils/Uttaksdagen';
import { Forelder } from 'app/types/Forelder';
import dayjs from 'dayjs';
import { isOverfringsperiode, isUttaksperiode, Periode, Periodetype, Uttaksperiode } from 'uttaksplan/types/Periode';
import { StnadskontoType } from 'uttaksplan/types/StnadskontoType';
import { andreAugust2022ReglerGjelder, tidperiodeOverlapperDato } from './dateUtils';
import { intlUtils, TidsperiodeDate } from '@navikt/fp-common';
import { finnAntallDagerTrekke } from 'app/steps/uttaksplan-info/utils/uttaksPlanStatus';
import { Situasjon } from 'app/types/Situasjon';
import { IntlShape } from 'react-intl';

export const ANTALL_UTTAKSDAGER_FAR_MEDMOR_RUNDT_FDSEL = 10;
const ANTALL_DAGER_TO_UKER = 2 * 7;
const ANTALL_DAGER_SEKS_UKER = 6 * 7;

export const gjelderWLBReglerFarMedmorRundtFdsel = (
    familiehendelsesdato: Date,
    skerErFarEllerMedmor: boolean,
    morHarRett: boolean,
    situasjon: Situasjon
) => {
    const gjelderWLB = andreAugust2022ReglerGjelder(familiehendelsesdato);
    return gjelderWLB && skerErFarEllerMedmor && morHarRett && situasjon === 'fdsel';
};

export const isUttaksperiodeFarMedmorMedValgForUttakRundtFdsel = (periode: Periode): boolean => {
    return (
        isUttaksperiode(periode) &&
        periode.forelder === Forelder.farMedmor &&
        periode.konto === StnadskontoType.Fedrekvote &&
        !!periode.erMorForSyk === false &&
        periode.morsAktivitetIPerioden === undefined &&
        !!periode.nskerFlerbarnsdager === false &&
        periode.nskerSamtidigUttak === true
    );
};

export const isUttaksperiodeFarMedmorPgaFdsel = (
    periode: Periode,
    familiehendelsesdato: Date,
    termindato: Date | undefined
): boolean => {
    return (
        isUttaksperiodeFarMedmorMedValgForUttakRundtFdsel(periode) &&
        starterTidsperiodeInnenforToUkerFrFdselTilSeksUkerEtterFdsel(
            periode.tidsperiode,
            familiehendelsesdato,
            termindato
        )
    );
};

export const isUttaksperiodeBareFarMedmorHarRett = (periode: Periode, morHarRett: boolean): boolean => {
    return isUttaksperiode(periode) && periode.forelder === Forelder.farMedmor && !morHarRett;
};

export const getFrsteUttaksdag2UkerFrFdsel = (familiehendelsesdato: Date, termindato: Date | undefined): Date => {
    const terminEllerFamHendelsesdatoMinusToUker =
        termindato !== undefined
            ? dayjs(termindato).subtract(ANTALL_DAGER_TO_UKER, 'day')
            : dayjs(familiehendelsesdato).subtract(ANTALL_DAGER_TO_UKER, 'day');
    const datoRegneFra = dayjs.min(terminEllerFamHendelsesdatoMinusToUker, dayjs(familiehendelsesdato));
    return Uttaksdagen(datoRegneFra.toDate()).denneEllerNeste();
};

export const getSisteUttaksdag6UkerEtterFdsel = (familiehendelsesdato: Date): Date => {
    const frsteUttaksdagForPeriodeEtterFdsel = Uttaksdagen(familiehendelsesdato).denneEllerNeste();
    return Uttaksdagen(
        dayjs(frsteUttaksdagForPeriodeEtterFdsel).add(ANTALL_DAGER_SEKS_UKER, 'day').toDate()
    ).forrige();
};

export const starterTidsperiodeEtter2UkerFrFdsel = (
    tidsperiode: any,
    familiehendelsesdato: Date,
    termindato: Date | undefined
): boolean => {
    const frsteUttaksdagToUkerFrFdsel = getFrsteUttaksdag2UkerFrFdsel(familiehendelsesdato, termindato);
    return dayjs(tidsperiode.fom).isSameOrAfter(frsteUttaksdagToUkerFrFdsel, 'day');
};

export const starterUttaksperiodeFrFdsel = (periode: Periode, familiehendelsesdato: Date): boolean => {
    return isUttaksperiode(periode) && dayjs(periode.tidsperiode.fom).isBefore(familiehendelsesdato, 'day');
};

export const slutterTidsperiodeInnen6UkerEtterFdsel = (tidsperiode: any, familiehendelsesdato: Date): boolean => {
    const sisteUttaksdag6UkerEtterFdsel = getSisteUttaksdag6UkerEtterFdsel(familiehendelsesdato);
    return dayjs(tidsperiode.tom).isSameOrBefore(sisteUttaksdag6UkerEtterFdsel, 'day');
};

export const starterTidsperiodeInnenforToUkerFrFdselTilSeksUkerEtterFdsel = (
    tidsperiode: TidsperiodeDate,
    familiehendelsesdato: Date,
    termindato: Date | undefined
) => {
    return (
        starterTidsperiodeEtter2UkerFrFdsel(tidsperiode, familiehendelsesdato, termindato) &&
        dayjs(tidsperiode.fom).isSameOrBefore(getSisteUttaksdag6UkerEtterFdsel(familiehendelsesdato), 'day')
    );
};

export const getFarMedmorUttakRundtFdsel = (
    perioder: Periode[],
    familiehendelsesdato: Date,
    termindato: Date | undefined
): Periode[] => {
    return perioder.filter((p) => isUttaksperiodeFarMedmorPgaFdsel(p, familiehendelsesdato, termindato));
};

export const erFarMedmorSinWLBTidsperiodeRundtFdsel = (
    tidsperiode: TidsperiodeDate,
    familiehendelsesdato: Date,
    periodetype: Periodetype,
    konto: StnadskontoType,
    erFarEllerMedmor: boolean,
    termindato: Date | undefined,
    situasjon: Situasjon
): boolean => {
    return (
        tidsperiode !== undefined &&
        isValidTidsperiode(tidsperiode) &&
        erFarEllerMedmor &&
        situasjon === 'fdsel' &&
        andreAugust2022ReglerGjelder(familiehendelsesdato) &&
        periodetype === Periodetype.Uttak &&
        (konto === StnadskontoType.Fedrekvote ||
            konto === StnadskontoType.Foreldrepenger ||
            konto === StnadskontoType.AktivitetsfriKvote) &&
        starterTidsperiodeInnenforToUkerFrFdselTilSeksUkerEtterFdsel(tidsperiode, familiehendelsesdato, termindato)
    );
};

export const getLengdePForeslttWLBUttakFarMedmor = (familiehendelsesdato: Date, startDatoUttak: Date): number => {
    const sisteUttaksDagFrFdsel = getSisteUttaksdag6UkerEtterFdsel(familiehendelsesdato);
    const antallUttaksdagerFraStartDato = finnAntallDagerTrekke({
        tidsperiode: { fom: startDatoUttak, tom: sisteUttaksDagFrFdsel },
    } as Periode);
    return Math.min(antallUttaksdagerFraStartDato, ANTALL_UTTAKSDAGER_FAR_MEDMOR_RUNDT_FDSEL);
};

export const appendPeriodeNavnHvisUttakRundtFdselFarMedmor = (
    intl: IntlShape,
    periodeNavn: string,
    periode: Periode,
    situasjon: Situasjon,
    familiehendelsesdato: Date,
    termindato: Date | undefined
): string => {
    return situasjon === 'fdsel' && isUttaksperiodeFarMedmorPgaFdsel(periode, familiehendelsesdato, termindato)
        ? periodeNavn + intlUtils(intl, 'rundtFdsel')
        : periodeNavn;
};

export const farMedmorsTidsperiodeSkalSplittesPFamiliehendelsesdato = (
    periode: Periode,
    familiehendelsesdato: Date,
    morHarRett: boolean,
    termindato: Date | undefined
) => {
    return (
        (isUttaksperiodeFarMedmorPgaFdsel(periode, familiehendelsesdato, termindato) ||
            isUttaksperiodeBareFarMedmorHarRett(periode, morHarRett)) &&
        tidperiodeOverlapperDato(periode.tidsperiode, familiehendelsesdato)
    );
};

export const getPerioderMedUttakRundtFdsel = (
    uttaksplan: Periode[],
    familiehendelsesdato: Date,
    termindato: Date | undefined
): Uttaksperiode[] => {
    return uttaksplan.filter(
        (p) =>
            (isUttaksperiode(p) || isOverfringsperiode(p)) &&
            starterTidsperiodeInnenforToUkerFrFdselTilSeksUkerEtterFdsel(
                p.tidsperiode,
                familiehendelsesdato,
                termindato
            )
    ) as [];
};

export const uttaksperiodeKanJusteresVedFdsel = (
    nskerJustertUttakVedFdsel: boolean | undefined,
    termindato: Date | undefined,
    uttaksperiodeFom: Date
) => {
    return !!nskerJustertUttakVedFdsel && termindato !== undefined && dayjs(uttaksperiodeFom).isSame(termindato);
};
